export function deleItem(path, id) {//
    return new Promise((resolve, reject) => {
        if (path === "/notice_list") {//
            http('/notice/noticeDel', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/repair_manage") {//
            http('/repair/repairDel', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/quarters_list") {//
            http('/community/community_delete', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/household_list") {//
            http('/community/delete_household', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/water_list") {//
            http('/pay/water_del', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/gas_list") {//
            http('/pay/gas_del', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/electricity_list") {//
            http('/pay/electric_del', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/property_list") {//
            http('/pay/property_del', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/garbage_list") {//
            http('/pay/garbage_del', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/parking_list") {//
            http('/park/delete_park', { method: 'POST', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
        if (path === "/") {//
            http('/owner/owner_delete', { method: 'get', data: { id: id } }).then(res => {
                resolve(res)
            }).catch(res => {
                reject(res)
            })
        }
    })
}